* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

body {
  font-family: 'Work Sans', sans-serif;
  font-weight: 100;
  overflow-x: hidden;
  background-color: #eee;
}

div section.front {
  height: 100vh;
  position: relative;
}

div section.front div.title {
  position: absolute;
  top: 50%;
  padding: 5% 0 5% 10%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100vw;
  left: 0;
  background-image: url("../public/img/g/10.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

div section.front div.title::after {
  display: block;
  content: '';
  left: 0;
  position: absolute;
  top: 10%;
  width: 100%;
  height: 10px;
  background-color: #eee;
  z-index: 10;
}

div section.front div.title::before {
  display: block;
  content: '';
  left: 0;
  position: absolute;
  bottom: 10%;
  width: 100%;
  height: 10px;
  background-color: #eee;
  z-index: 10;
}

div section.front div.title h1 {
  font-weight: 100;
  font-size: 120px;
  line-height: 150px;
}

div section.front div.title p {
  font-size: 48px;
  line-height: 92px;
}

section.gallery {
  position: relative;
  height: 230vh;
}

section.gallery::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 10vh;
  width: 90vw;
  right: 0;
  height: 5px;
  background-color: #555;
}

section.gallery.g {
  margin-top: 5%;
  height: 280vh;
}

section.gallery.g::after {
  content: '';
  display: block;
  position: absolute;
  bottom: 10vh;
  left: 0;
  width: 90vw;
  height: 5px;
  background-color: #555;
}

section.gallery h1 {
  font-size: 64px;
  font-weight: 100;
  position: absolute;
  z-index: 3;
}

section.gallery h1.g {
  top: 14vw;
  left: 10vw;
}

section.gallery h1.j {
  top: 14vw;
  right: 20vw;
}

section.gallery ul.wrap {
  position: relative;
}

section.gallery ul.wrap::after {
  clear: both;
  display: block;
  content: '';
}

section.gallery ul.wrap > li {
  float: left;
}

section.gallery ul.wrap > li > ul {
  position: relative;
}

section.gallery ul.wrap > li > ul li {
  width: 20vw;
  height: 28vw;
  padding: 2vw;
}

section.gallery ul.wrap > li > ul li img {
  width: 16vw;
  height: 24vw;
  -o-object-fit: cover;
     object-fit: cover;
}

section.gallery > ul.g {
  left: 20vw;
}

section.gallery > ul.g > li:nth-of-type(odd) > ul {
  top: 100px;
}

section.gallery > ul.j > li:nth-of-type(even) > ul {
  top: 100px;
}
